
.QR{
    z-index: 101;
    position: fixed;
    right: 1em;
    top: 1em;
    padding-right: .5em;
    padding-left: .5em;
    padding-top: .25em;
    padding-bottom: .25em;
    color: #fff;
    font-weight: 100;
    border: 1px #e9c46a solid;
    border-radius: 5px;
}

.qr-container{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.QR-image{
    display: none;
    border: 1px #fff solid;
    border-radius: 5px;
}

.QR-image img{
    width: 250px;
}